import { textHelper } from './../helpers';

function getCommonResult(userInfo) {
  const score = userInfo.score;
  const scoresText = textHelper.getDeclension(userInfo.score, '', '', '');

  return {
    sticker: 'fox_104',
    title: '!',
    text: `  ${score} ${scoresText}    !`
  }
}

/**
 * @param {object} userInfo
 * @param {number} userInfo.aValue
 * @param {number} userInfo.bValue
 * @returns {object}
 */
export function getUserResults(userInfo) {
  return getCommonResult(userInfo);
}



